import React, { useEffect, useState } from 'react'
import AddBanner from '../../../reuseableComponents/AddBanner'
import CreatedKRATable from './CreatedKRATable'
import AddNewKRA from './AddNewKRA';
import AssignKRAModal from './AssignKRAModal';
import ViewKRAParameter from './ViewKRAParameter';
import { useDispatch, useSelector } from 'react-redux';
import { departmentAction, financialYearActions, performanceManagementActions } from '../../../../core/resource/actions';
import { pagination } from '../../../../utils/Constants.config';

const createdKRAData = [
    {
        kra: "Lorem Ipsum",
        department: "Product Development",
        ratingType: "Rating",
        status: "completed",
        action: "Assign",
    },
    {
        kra: "Lorem Ipsum",
        department: "HZL CVT Operation",
        ratingType: "Target_Achievement",
        status: "new",
        action: "Assign",
    },
    {
        kra: "Lorem Ipsum",
        department: "Product Development",
        ratingType: "Rating",
        status: "completed",
        action: "Assign",
    },
    {
        kra: "Lorem Ipsum",
        department: "HZL CVT Operation",
        ratingType: "Target_Achievement",
        status: "inProcess",
        action: "Assign",
    },
    {
        kra: "Lorem Ipsum",
        department: "Product Development",
        ratingType: "Rating",
        status: "new",
        action: "Assign",
    },
    {
        kra: "Lorem Ipsum",
        department: "HZL CVT Operation",
        ratingType: "Target_Achievement",
        status: "new",
        action: "Assign",
    },
];

const CreateKRA = () => {
    const dispatch = useDispatch()
    const {
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    } = useSelector(state => state.department)
    const {
        getFinancialYearListRes,
        getFinancialYearListError,
        getFinancialYearListPending
    } = useSelector(state => state.financialYear)

    const {
        createKraPending,
        createKraRes,
        createKraError,

        getKraDetailsPending,
        getKraDetailsRes,
        getKraDetailsError,

        updateKraDetailPending,
        updateKraDetailRes,
        updateKraDetailError,

        deleteKraDetailPending,
        deleteKraDetailRes,
        deleteKraDetailError

    } = useSelector(state => state.performanceManagement)
    const [isShowAddNewKRA, setIsShowAddNewKRA] = useState(false)
    const [isShowAssignKRA, setIsShowAssignKRA] = useState(false)
    const [selectedAssignKRA, setSelectedAssignKRA] = useState('')
    const [isShowViewKRAParameter, setIsShowViewKRAParameter] = useState(false)
    const [allDepartments, setAllDepartments] = useState('')
    const [financialYearListData, setFinancialYearListData] = useState('')
    const [selectedKraData, setSelectedKraData] = useState('')
    const [allKraDetailsData, setAllKraDetailsData] = useState('')
    const [currentPage, setCurrentPage] = useState(1)

    const toggleAddNewKRA = () => {
        setIsShowAddNewKRA(!isShowAddNewKRA)
    }
    const toggleAssignKRA = (data) => {
        if (data) {
            setSelectedAssignKRA(data)
        }
        setIsShowAssignKRA(!isShowAssignKRA)
    }

    const toggleViewKRAParameter = (data) => {
        setIsShowViewKRAParameter(!isShowViewKRAParameter)
    }


    const buttons = [{ classNames: "btn-primary", onClick: toggleAddNewKRA, text: 'New KRA' }]


    /* Get All Departments */
    useEffect(() => {
        getAllDepartments()
        getFinancialYearListData()
    }, [])
    const getAllDepartments = () => {
        const departmentResposnse = getDepartmentListRes?.data
        if (!departmentResposnse?.length) {
            dispatch(departmentAction.getDepartmentListRequest());
        } else {
            setAllDepartments(departmentResposnse)
        }
    }

    useEffect(() => {
        if (!getDepartmentListPending) {
            if (getDepartmentListError) {
                return;
            }
            if (getDepartmentListRes) {
                if (getDepartmentListRes?.code === 200) {
                    const departmentResponse = getDepartmentListRes?.data
                    setAllDepartments(departmentResponse)
                }
            }
        }
    }, [
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    ]);


    /* List of Financial Year */
    const getFinancialYearListData = () => {
        dispatch(financialYearActions.getFinancialYearListRequest())
    }
    useEffect(() => {
        if (!getFinancialYearListPending) {
            if (getFinancialYearListError) {
                return;
            }
            if (getFinancialYearListRes) {
                if (getFinancialYearListRes?.code === 200) {
                    setFinancialYearListData(getFinancialYearListRes?.data)

                }
            }
        }
    }, [
        getFinancialYearListRes,
        getFinancialYearListError,
        getFinancialYearListPending
    ]);

    /* Create Update Kra Details */
    const handleCreateUpdateKra = (params) => {
        if (selectedKraData) {

            dispatch(performanceManagementActions.updateKraDetailRequest(params));
        }
        else {
            dispatch(performanceManagementActions.createKraParameterRequest(params));
        }
    }
    useEffect(() => {
        if (!createKraPending) {
            if (createKraError) {
                dispatch(performanceManagementActions.handleClearPMSStore("HANDLE_CLEAR_CREATE_KRA_STORE"))
                return;
            }
            if (createKraRes) {
                if (createKraRes?.code === 200) {
                    dispatch(performanceManagementActions.handleClearPMSStore("HANDLE_CLEAR_CREATE_KRA_STORE"))
                    dispatch(performanceManagementActions.handleClearPMSStore("HANDLE_CLEAR_GET_KRA_DETAILS_STORE"))
                    getKraDetailsData()

                }
            }
        }
    }, [
        createKraPending,
        createKraRes,
        createKraError,
    ]);

    useEffect(() => {
        if (!updateKraDetailPending) {
            if (updateKraDetailError) {
                return;
            }
            if (updateKraDetailRes) {
                if (updateKraDetailRes?.code === 200) {
                    dispatch(performanceManagementActions.handleClearPMSStore("HANDLE_CLEAR_UPDATE_KRA_DETAIL_STORE"))
                    dispatch(performanceManagementActions.handleClearPMSStore("HANDLE_CLEAR_GET_KRA_DETAILS_STORE"))
                    // setFinancialYearListData(createKraRes?.data)
                    getKraDetailsData()
                }
            }
        }
    }, [
        updateKraDetailPending,
        updateKraDetailRes,
        updateKraDetailError,
    ]);


    /* Get Kra Details Data */
    useEffect(() => {
        getKraDetailsData()
    }, [currentPage])

    const getKraDetailsData = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(performanceManagementActions.getKpiDetailsRequest(params));
    }

    useEffect(() => {
        if (!getKraDetailsPending) {
            if (getKraDetailsError) {
                return;
            }
            if (getKraDetailsRes) {
                if (getKraDetailsRes?.code === 200) {
                    setAllKraDetailsData(getKraDetailsRes?.data)
                }
            }
        }
    }, [
        getKraDetailsRes,
        getKraDetailsError,
        getKraDetailsPending
    ]);

    /* Delete  Kra Detail */
    // const toggleDeleteKraDeleteModal = (id) => {
    //     if (id) {
    //         setDeleteDeleteKraId(id)
    //     }
    //     setIsShowDeleteDeleteKraModal(!isShowDeleteDeleteKraModal)
    // }

    // const handleDeleteDeleteKra = (type) => {
    //     if (type) {
    //         const params = {
    //             _id: deleteDeleteKraId
    //         }
    //         dispatch(dropdownMasterActions?.deleteMasterDeleteKraRequest(params))
    //     } else {
    //         toggleDeleteKraDeleteModal()
    //     }
    // }

    // useEffect(() => {
    //     if (!deleteKraDetailPending) {
    //         if (deleteKraDetailError) {
    //             dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_DELETE_MASTER_DROPDOWN_CATEGORY_STORE"))
    //             return;
    //         }
    //         if (deleteKraDetailRes) {
    //             if (deleteKraDetailRes?.code === 200) {
    //                 toggleDeleteKraDeleteModal()
    //                 if (categoryCurrentPage > 1 && allDeleteKra?.data?.length === 1) {
    //                     setCategoryCurrentPage((pre => pre -= 1))
    //                 } else {
    //                     getMasterDeleteKraData()
    //                 }
    //                 dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_DELETE_MASTER_DROPDOWN_CATEGORY_STORE"))
    //             }
    //         }
    //     }
    // }, [
    //     deleteKraDetailRes,
    //     deleteKraDetailError,
    //     deleteKraDetailPending
    // ]);


    return (
        <div>
            <AddBanner
                titleName={"Add New Key Responsibility Areas (KRA)"}
                buttons={buttons}
            />
            <CreatedKRATable
                createdKRAData={createdKRAData}
                toggleAssignKRA={toggleAssignKRA}
                toggleViewKRAParameter={toggleViewKRAParameter}
                financialYearListData={financialYearListData}
            />
            {isShowAddNewKRA && <AddNewKRA
                openModel={isShowAddNewKRA}
                handleOpenClose={toggleAddNewKRA}
                allDepartmentList={allDepartments}
                financialYearListData={financialYearListData}
            />}
            {isShowAssignKRA && <AssignKRAModal
                openModel={isShowAssignKRA}
                handleOpenClose={toggleAssignKRA}
                selectedAssignKRA={selectedAssignKRA}
            />}
            {isShowViewKRAParameter && <ViewKRAParameter
                openModel={isShowViewKRAParameter}
                handleOpenClose={toggleViewKRAParameter}
            // selectedAssignKRA={selectedAssignKRA}
            />}
        </div>
    )
}

export default CreateKRA
